.contacts {
  padding: 0 50px 50px 50px;

  @media (max-width: $vp-980) {
    padding: 0 40px 40px 40px;
  }

  @media (max-width: $vp-767) {
    padding: 0 30px 30px 30px;
  }

  &__title {
    margin: 0 0 30px 0;

    text-align: center;

    @media (max-width: $vp-767) {
      margin: 0 0 25px 0;
    }
  }

  &__list {
    display: grid;

    gap: 30px;
    grid-template-columns: repeat(3, 1fr);
  }

  &__item {
    display: flex;
    justify-content: center;
  }

  &__link {
    display: flex;
    flex-direction: column;
    align-items: center;

    gap: 20px;

    @media (hover: hover) {
      &:hover svg {
        fill: $color-free-speech-magenta;
      }

      &--gmail:hover svg {
        filter: invert(100%);
      }
    }

    & svg {
      @media (max-width: $vp-980) {
        width: 60px;
        height: 60px;
      }

      @media (max-width: $vp-767) {
        width: 50px;
        height: 50px;
      }
    }
  }
}
